<template>
  <div class="goods-detail">
    <navigation-bar
      :isDefault="false"
      :isShowBack="false"
      @onLeftClick="onBackClick"
      :navBarStyle="navBarStyle"
    >
      <template v-slot:nav-left>
        <div class="goods-detail-nav-left">
          <img src="@img/back-2.svg" :style="{ opacity: leftImgOpacity }" />
          <img src="@img/back-white.svg" :style="{ opacity: navBarOpacity }" />
        </div>
      </template>

      <template v-slot:nav-center>
        <p class="goods-detail-nav-title" :style="{ opacity: navBarOpacity }">
          商品详情
        </p>
      </template>
    </navigation-bar>

    <div class="goods-detail-content" @scroll="onScrollChange">
      <my-swiper
        :height="SWIPER_IMAGE_HEIGHT + 'px'"
        :swiperImgs="goodsData.swiperImgs"
      ></my-swiper>
      <div class="goods-detail-content-desc">
        <!-- 价格名称 -->
        <div class="goods-detail-content-desc-item">
          <p class="goods-detail-content-desc-item-price">
            ￥{{ goodsData.price }}
          </p>
          <p class="goods-detail-content-desc-item-name">
            <direct v-if="goodsData.isDirect"></direct>
            {{ goodsData.name }}
          </p>
        </div>
        <!-- 选中服务 -->
        <div class="goods-detail-content-desc-item">
          <p class="goods-detail-content-desc-item-select">
            已选 <span class="single-row-text">{{ goodsData.name }}</span>
          </p>

          <div class="goods-detail-content-desc-item-support">
            <ul class="goods-detail-content-desc-item-support-list">
              <li
                class="goods-detail-content-desc-item-support-list-item"
                v-for="(item, index) in supportDate"
                :key="index"
              >
                <img src="@img/support.svg" alt="" srcset="" />
                <span>{{ item }}</span>
              </li>
            </ul>
          </div>
        </div>
        <!-- 详情 -->
        <div class="goods-detail-content-desc-detail">
          <img
            v-for="(item, index) in goodsData.detailImgs"
            :key="index"
            :src="item"
            alt=""
            srcset=""
          />
        </div>
      </div>
    </div>
    <!-- 下方条 -->
    <div class="goods-detail-buy z-index-max">
      <div class="goods-detail-buy-shopping" @click="toShopping">
        <img src="@img/home-n.svg" alt="" />
      </div>

      <div class="goods-detail-buy-add" @click="onAddGoodsClick">
        <span>加入购物车</span>
      </div>
      <div class="goods-detail-buy-now" @click="onBuyClick(this.goodsData)">
        <span>立即购买</span>
      </div>
    </div>
  </div>
</template>

<script>
import { useRouter, useRoute } from "vue-router";
import NavigationBar from "@c/currency/NavigationBar";
import MySwiper from "@c/swiper/MySwiper";
import direct from "@c/goods/Direct";
// import Parallax from "@c/parallax/Parallax";
export default {
  name: "goodsDetail",
  components: {
    NavigationBar,
    MySwiper,
    direct,
    // Parallax,
  },
  data: function () {
    return {
      //swiper图片高度
      SWIPER_IMAGE_HEIGHT: 364,
      //滑动高度
      ANCHOR_SCROLL_TOP: 310,
      // 商品数据
      goodsData: {},
      // 滚动值
      scrollTopValue: -1,
      supportDate: [
        "可配送海外",
        "京东发货&售后",
        "京准达",
        "211限时达",
        "可自提",
        "不可使用优惠卷",
      ],
      parallaxScroll: 0,
    };
  },
  setup() {
    const router = useRouter(),
      route = useRoute();
    const handleGoods = () => {
      const goods = route.query.goods;
      return goods;
    };
    const onBuyClick = (item) => {
      const obj = JSON.stringify(item);
      router.push({
        name: "buy",
        // path: "/buy",
        query: {
          goods: obj,
          routerType: "push",
        },
      });
    };
    const onBackClick = () => {
      router.go(-1);
      // router.push({
      //   name: "goodsList",
      // path: "/buy",
      // query: {
      //   goods: obj,
      //   routerType: "",
      // },
      // });
    };
    const toShopping = () => {
      router.push({
        name: "Main",
        // path: "/buy",
        // query: {
        //   to: "shopping",
        // },
      });
    };
    return { handleGoods, onBuyClick, onBackClick, toShopping };
  },
  //添加数据
  created() {
    this.goodsData = JSON.parse(this.handleGoods());
  },
  mounted() {
    // console.log(this.goodsData);
  },
  methods: {
    //页面滑动距离
    onScrollChange($e) {
      this.parallaxScroll = $e.target.scrollTop;
    },
    //添加商品到购物车
    onAddGoodsClick() {
      this.$store.commit("addShoppingData", this.goodsData);
      alert("添加成功");
    },
  },
  //navigationBar的显示样式 透明度
  computed: {
    leftImgOpacity() {
      return 1 - this.parallaxScroll / this.ANCHOR_SCROLL_TOP;
    },
    navBarOpacity() {
      return 1 - this.leftImgOpacity;
    },
    navBarStyle() {
      return {
        backgroundColor: "rgba(216, 30, 6, " + this.navBarOpacity + ")",
        position: "fixed",
        top: 0,
      };
    },
  },
};
</script>

